.Comment {
    margin-top: 10px;
    margin-bottom: 10px;
    width: 95%;
    min-height: 110px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content-content: space-around;
    align-items: center;
}

.Comment #user_avatar {
    width: 80px;
    min-height: 80px;
    border-radius: 100%;
}

.Comment #comment_text {
    width: 80%;
    min-height: 100px;
    border-radius: 25px;

    display: flex;
    justify-content: center;
    align-items: center;

    padding: 10px;
    margin-left: 15px;
    text-indent: 15px;
    background: #FFE1E1;
}

.Comment #nickname {
    min-width: 100px;
    height: 20px;
}