.AdminPage {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: start;
}

.AdminPage .leftWrappper {
    width: 39vw;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.AdminPage .leftWrappper .avatarWrapper {
    right: 30%;
    width: 75%;
    height: 59%;
}

.AdminPage .leftWrappper .avatarWrapper #addAvatar {
    position: absolute;
    width: 100px;
    height: 100px;
    left: 17%;
    top: 37%
}


.AdminPage .rightWrapper {
    width: 59vw;
    height: 90vh;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.AdminPage .rightWrapper #info {
    width: 100%;
    height: 15vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}

.AdminPage .rightWrapper #nameField {
    width: 45%;
    height: 40%;
    background: #F74E51;
    opacity: 85%;
    border-radius: 15px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Helvetica-black';
    color: white;
}

.AdminPage .rightWrapper textarea {
    width: 90%;
    height: 40%;
    text-indent: 10px;
}

.AdminPage .rightWrapper input {
    width: 90%;
    height: 6%;
    border-radius: 15px;
    text-indent: 10px;
}

.AdminPage .rightWrapper #addPic {
    width: 10%;
    height: 10%;
    background: url("../../static/media/add_image.png");
    background-size: cover;
    background-repeat: no-repeat;

}

.AdminPage .rightWrapper #addPic input {
    opacity: 0;
    cursor: pointer;
    width: 100%;
    height: 100%;
}

.AdminPage .rightWrapper #infoBlock {
    width: 25%;
    height: 15%;
    background: #F74E51;
    opacity: 85%;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}