.avatar {
    position: absolute;
    top: 63px;
    left: 30px;
}

#add_avatar_btn {
    position: relative;
    background: url("../../static/media/change_avatar_pic.png");
    background-repeat: no-repeat;
    width: 216px;
    height: 215px;
    border: none;
    top: 213px;
    left: 140px;
}

.info_wrapper {
    position: absolute;
    width: 40%;
    height: 45%;
    top: 10%;
    left: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    font-family: 'Helvetica-black';
}

.info_wrapper #name {
    width: 90%;
    height: 13%;
    background: #F74E51;
    opacity: 85%;
    border-radius: 15px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid black 2px;


}

.info_wrapper #email {
    width: 90%;
    height: 13%;
    background: #F74E51;
    opacity: 85%;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid black 2px;

}

.info_wrapper #ChangeName {
    width: 70%;
    height: 10%;
    background: #FF3535;
    border-radius: 15px;
    text-align: center;
    font-family: Monserrat;

}

.info_wrapper #ChangeName:hover {
    background: #E77D7D;

}

.UserCommentBoxesContainer {
    position: absolute;
    width: 92vw;
    min-height: 30vh;
    top: 58%;
    left: 2%;
    padding: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}