.article {

}

.picContainer {
    position: absolute;
    width: 80vw;
    height: 50vh;
    left: 10vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.article #mainPic {
    position: absolute;
    min-width: 45vw;
    min-height: 35vh;
    max-width: 70vw;
    max-height: 50vh;
}

.article .p-wrapper {
    position: absolute;
    min-height: 35vh;
    top: 62%;
    width: 80vw;
    left: 10vw;
    background: #EA1010;
}

.article h1 {
    color: white;
    font-family: 'Helvetica-black';
    font-size: 43px;
    padding-bottom: 1%;
    margin-top: 10px;
    text-align: center;
}

p {
    padding-left: 8%;
    width: 83%;
    left: 16%;
    text-indent: 25px;
    color: white;
    font-family: 'Monserrat';
    font-size: 25px;
    line-height: 37px;
}

.comment-wrapper {
    width: 95%;
    min-height: 30vh;
    margin-left: 2.4%;
    display: flex;
    flex-direction: column;
    align-items: center;
    just-content: space-around;

}

.comment-wrapper #existingComments {
    width: 63%;
    min-height: 10vh;
    background: #FFBABA;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    align-content: space-around;
    align-items: center;
}

.comment-wrapper #commentMaker {
    width: 63%;
    min-height: 10vh;
    border-radius: 30px;
    padding-top: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.comment-wrapper #SendBtn {
    margin-top: 1%;
    width: 18%;
    height: 30px;
    border-radius: 35px;
    background: #A82525;
}

textarea {
    width: 90%;
    min-height: 10vh;
    margin: 10px;
    border-radius: 15px;
}