.NewsMiniBlock {
    width: 27%;
    height: 100%;
    margin-right: 10px;

    display: flex;

    flex-direction: row;
    align-items: center;
    justify-content: end;
    text-decoration: none;
    margin-left: 10px;
}

.NewsMiniBlock img {
    width: 60%;
    height: 100%;
    object-fit: cover
}

.NewsMiniBlock div {
    width: 40%;
    height: 98%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    background: #CFD2EC;
    padding: 3px;
    overflow: hidden;
}

.NewsMiniBlock div h4 {
    text-align: center;
    font-family: 'Helvetica-black';
    font-size: 0.8vw;
    color: black;
}

.NewsMiniBlock div p {
    font-family: Monserrat;
    font-size: 1.3vh;
    line-height: 1.2vh;
    color: black;
    padding: 3px;
    text-indent: 1px;
    text-align: center;
}

.NewsMainBlock {
    width: 45%;
    height: 25vh;
    margin-right: 10px;

    display: flex;

    flex-direction: row;
    align-items: center;
    justify-content: end;
    text-decoration: none;
    margin-left: 3vw;
    margin-top: 4vh;
}

.NewsMainBlock img {
    width: 40%;
    height: 100%;
    object-fit: cover
}

.NewsMainBlock div {
    width: 60%;
    height: 98%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background: #CFD2EC;
    padding: 3px;
    overflow: hidden;
}

.NewsMainBlock div h4 {
    text-align: center;
    font-family: 'Helvetica-black';
    font-size: 15px;
    color: black;
}

.NewsMainBlock div p {
    font-family: Monserrat;
    font-size: 15px;
    line-height: normal;
    color: black;
    padding: 3px;
    text-indent: 1px;
    text-align: center;
}